<template>
  <div>
        <md-card class="md-elevation-5 my-hover">
          <md-card-content>
            <img src="/static/loli.gif"/>
            <p style="color: rgba(0,0,0,1);font-size: 20px;text-align: right">修改密码吖↖</p>

            <md-field :class="isPhone(form.phone_number)">
              <label>手机号码</label>
              <md-input :disabled="false" type="number" v-model="form.phone_number">
              </md-input>
              <span class="md-error">这不是一个正确的手机号码！</span>

            </md-field>

            <md-field >
              <label>手机验证码</label>
              <md-input>
              </md-input>
              <md-button class="my-raised my-hover">获取手机验证码</md-button>
            </md-field>

            <md-field>
              <label>密码</label>
              <md-input type="password" v-model="form.password">
              </md-input>
            </md-field>

          </md-card-content>
          <md-card-actions>
            <md-button class="my-raised my-hover" style="background-color: white;color: black" to="/login">转向登陆？
            </md-button>
            <md-button class="my-raised my-hover" style="background-color: white;color: black"
                       to="/register">转向注册？
            </md-button>
            <md-button class="my-raised my-hover" style="background-color: white;color: black"
                       v-on:click="resetPsw">修改密码
            </md-button>
          </md-card-actions>
        </md-card>
      </div>

</template>

<script>

  export default {
    name: 'Index',
    components: {
    },
    data() {
      return {
        form: {
          phone_number: null,
          password: null
        },
        words: ['<b style="color: orange">忘记密码了嘛~</b>'],
      }
    },
    computed: {},
    methods: {
      isPhone(phone_number) {
        if (phone_number)
          return this.$myapi.isPhone(phone_number) ? '' : 'md-invalid'
        else return ''
      },
      // pull和build项目
      resetPsw() {
        let _this = this
        // 是手机才让他修改密码
        // if (_this.isPhone(_this.form.phone_number) != null) {
        // } else {
        this.$myapi.post('/user/update', _this.form, function (res) {
          _this.$alert.show(100,"修改成功")
        })
        // }
      },
    },
    created() {
      this.$store.commit("setWords", ['<b style="color: orange">哼，再不登录我就生气辣！</b>', '<b style="color: blue">你在干什么呢？可以和我说说话吗？</b>'])
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  .my-raised {
    background-color: rgba(10, 10, 0, .1);
    border-radius: 7px;
  }

  .my-hover:hover {
    cursor: url('/static/mouse/mouse2.cur'), auto !important;
    background-color: rgba(255, 255, 255, .8);
  }

  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }

  .md-card {
    margin-top: 20px;
    margin-bottom: 20px;
    border-radius: 5px;
  }

</style>



